.container {
  border: 1px solid gray;
  padding: 20px;
  text-align: center;
  margin-bottom: 20px;
  border-radius: 5px;
}

input,
textarea {
  margin-bottom: 20px;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
}

textarea {
  height: 150px;
}

#emotion-select {
  width: 150px;
  padding: 5px;
  margin-bottom: 20px;
}

.submit-btn {
  width: 100%;
  padding: 10px;
  background-color: #2679F2;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  border: 0;
  cursor: pointer;
  border-radius: 10px;
}

.submit-btn:hover {
  background-color: #4FA4F7;
}

@media screen and (min-width: 769px) {
  input,
  textarea,
  .submit-btn { 
    width: 80%;
    transition: all .7s;
  }
}

@media screen and (max-width: 768px) {
  input,
  textarea,
  .submit-btn { 
    width: 100%;
    transition: all .7s;
  }
}
